import * as React from 'react'
import Logo from '../components/Logo/Logo'
import Navigation from '../components/Navigation/Navigation'
import Footer from '../components/Footer/Footer'
import AdoptionForm from '../components/AdoptionForm/AdoptionForm'
import Hero from '../components/Hero/Hero';
import 'bootstrap/dist/css/bootstrap.min.css'

const AdoptionFormPage = () => {
  return (
    <div>
      <Logo></Logo>
      <Navigation menuSelected="adopt-menu"></Navigation>
      <main>
      <Hero slideLabel="" slideDescription="" heroImageClass="contact-us-hero"></Hero>
        <AdoptionForm></AdoptionForm>
      </main>
      <Footer></Footer>
    </div>
  )
}

export const Head = () => <title>Adoption Form - Finn's Furever Feline Rescue</title>

export default AdoptionFormPage